.main {
    max-width: 700px;
    margin: 20px auto 0;
    padding: 0 20px;
    vertical-align: top;
}
.main .partsDice strong, .main .actionsDice strong{ 
    background-color: transparent;
    color: #333333;
    float: left;
}

.partsDice, .actionsDice{
    background-color: #ffd5d5;
    padding: .75rem;
    width: 100px;
    height: 100px;
    outline: 10px groove tomato;
    margin: 20px;
    display: inline-block;
}

.main .rollButton button{
    padding: 10px;
    border-radius: 5px;
    border: 0;
    background: #da552f;
    color: #FFF;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
}

.main .rollButton button:hover{
    opacity: 0.7;
}

